import React from 'react';
import PropTypes from 'prop-types';
import './styles.scss';

const PortfolioItem = (props, context) => {
  const {
    theme: { colorPrimary, textAlternate },
  } = context;

  if (props.render) return props.render;
  else
    return (
      <div className='portfolio-item' style={{ backgroundColor: colorPrimary, color: textAlternate }}>
        <div className='portfolio-item__info'>
          <div className='portfolio-item__title'>{props.title}</div>

          <div className='portfolio-item__desc'>{props.desc}</div>
          <div className='portfolio-item__icon'>
            {props.techStack}
            <div className='portfolio-item__links'>
              <a href={props.githubLink} style={{ color: textAlternate }}>
                GitHub Repo
              </a>
              {props.liveLink ? (
                <a href={props.liveLink} style={{ color: textAlternate }}>
                  Live Demo
                </a>
              ) : (
                ''
              )}
            </div>
          </div>

          {/* <div className='portfolio-item__img'>
            <img src={props.img} alt={props.title}></img>
          </div> */}
        </div>
      </div>
    );
};

PortfolioItem.contextTypes = {
  theme: PropTypes.any,
};

export default PortfolioItem;
