import React from 'react';
import { ButtonBack, ButtonNext, DotGroup, CarouselProvider, Slide, Slider } from 'pure-react-carousel';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleRight, faAngleLeft } from '@fortawesome/free-solid-svg-icons';
import PortfolioItem from '../PortfolioItem/PortfolioItem';
import PropTypes from 'prop-types';
import portfolioItems from '../PortfolioItem/portfolioitems';

import './styles.scss';

const ProjectCarousel = (props, context) => {
  const {
    theme: { bgPrimary, colorPrimary },
  } = props.theme;

  const renderPortfolioItem = (item, index) => {
    return (
      <Slide className='carousel__slide' index={index}>
        <PortfolioItem
          title={item.title}
          img={item.img}
          desc={item.desc}
          techStack={item.techStack}
          githubLink={item.githubLink}
          liveLink={item.liveLink}
        />
      </Slide>
    );
  };

  return (
    <CarouselProvider visibleSlides={1} totalSlides={4} step={1} naturalSlideWidth={400} naturalSlideHeight={500} infinite>
      <h1 className='carousel__headline'>Projects</h1>
      <div className='carousel__container'>
        <Slider className='carousel__slider'>{portfolioItems.map((item, index) => renderPortfolioItem(item, index))}</Slider>
        <ButtonBack className='carousel__buttonBack'>
          <FontAwesomeIcon icon={faAngleLeft} style={{ color: bgPrimary }} />
        </ButtonBack>
        <ButtonNext className='carousel__buttonNext'>
          <FontAwesomeIcon icon={faAngleRight} style={{ color: bgPrimary }} />
        </ButtonNext>
      </div>
    </CarouselProvider>
  );
};

PortfolioItem.contextTypes = {
  theme: PropTypes.any,
};

export default ProjectCarousel;
